<template>
  <div>
    <div class="flex justify-between items-center mb-6">
      <h2 class="text-3xl font-bold text-gray-800">인증 목록</h2>
      <div class="flex space-x-2">
        <input v-model="searchQuery" type="text" 
               placeholder="검색..." 
               class="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent">
      </div>
    </div>

    <div v-if="filteredBooks.length === 0" class="text-center py-16 bg-white rounded-lg shadow-md">
      <svg class="w-16 h-16 mx-auto text-gray-400 mb-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253"></path>
      </svg>
      <p class="text-gray-500 text-lg">아직 인증한 책이 없습니다</p>
      <button @click="$emit('navigate', 'register')" 
              class="mt-4 px-6 py-2 bg-indigo-600 text-white rounded-lg hover:bg-indigo-700 transition">
        첫 독서 인증하기
      </button>
    </div>

    <div v-else class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
      <div v-for="book in filteredBooks" :key="book.id"
           class="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-xl transition">
        <div class="bg-gradient-to-r from-indigo-500 to-purple-600 h-2"></div>
        <div class="p-6">
          <div class="flex justify-between items-start mb-3">
            <h3 class="text-xl font-bold text-gray-800">{{ book.title }}</h3>
            <button @click="$emit('delete', book.id)" 
                    class="text-red-500 hover:text-red-700 transition">
              <svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"></path>
              </svg>
            </button>
          </div>
          <p class="text-gray-600 mb-2">{{ book.author }}</p>
          <div class="flex items-center space-x-4 text-sm text-gray-500 mb-3">
            <span>{{ book.pages }}페이지</span>
            <span>{{ book.date }}</span>
          </div>
          <div class="flex mb-3">
            <span v-for="star in 5" :key="star" 
                  :class="star <= book.rating ? 'text-yellow-400' : 'text-gray-300'">
              ★
            </span>
          </div>
          <p v-if="book.review" class="text-gray-700 text-sm line-clamp-3">{{ book.review }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'

export default {
  name: 'BookList',
  props: {
    books: {
      type: Array,
      required: true
    }
  },
  emits: ['delete', 'navigate'],
  setup(props) {
    const searchQuery = ref('')

    const filteredBooks = computed(() => {
      if (!searchQuery.value) return props.books
      const query = searchQuery.value.toLowerCase()
      return props.books.filter(book => 
        book.title.toLowerCase().includes(query) || 
        book.author.toLowerCase().includes(query)
      )
    })

    return {
      searchQuery,
      filteredBooks
    }
  }
}
</script>

<style scoped>
.line-clamp-3 {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
</style>
