<template>
  <div class="min-h-screen bg-gray-50">
    <!-- 헤더 -->
    <header class="bg-indigo-600 text-white shadow-lg">
      <div class="container mx-auto px-4 py-6">
        <div class="flex items-center justify-between">
          <div class="flex items-center space-x-2">
            <svg class="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253"></path>
            </svg>
            <h1 class="text-2xl font-bold">독서 인증 시스템</h1>
          </div>
          <nav class="hidden md:flex space-x-6">
            <button @click="currentView = 'dashboard'" 
                    :class="{'border-b-2 border-white': currentView === 'dashboard'}"
                    class="pb-1 hover:border-b-2 hover:border-white transition">
              대시보드
            </button>
            <button @click="currentView = 'register'" 
                    :class="{'border-b-2 border-white': currentView === 'register'}"
                    class="pb-1 hover:border-b-2 hover:border-white transition">
              독서 인증
            </button>
            <button @click="currentView = 'list'" 
                    :class="{'border-b-2 border-white': currentView === 'list'}"
                    class="pb-1 hover:border-b-2 hover:border-white transition">
              인증 목록
            </button>
          </nav>
        </div>
      </div>
    </header>

    <div class="container mx-auto px-4 py-8">
      <!-- 대시보드 -->
      <Dashboard 
        v-if="currentView === 'dashboard'" 
        :books="books"
        @navigate="currentView = $event"
      />

      <!-- 독서 인증 폼 -->
      <RegisterBook 
        v-if="currentView === 'register'"
        @submit="handleBookSubmit"
      />

      <!-- 인증 목록 -->
      <BookList 
        v-if="currentView === 'list'"
        :books="books"
        @delete="deleteBook"
        @navigate="currentView = $event"
      />
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import Dashboard from './components/Dashboard.vue'
import RegisterBook from './components/RegisterBook.vue'
import BookList from './components/BookList.vue'

export default {
  name: 'App',
  components: {
    Dashboard,
    RegisterBook,
    BookList
  },
  setup() {
    const currentView = ref('dashboard')
    const books = ref([])

    const loadFromLocalStorage = () => {
      const saved = localStorage.getItem('readingBooks')
      if (saved) {
        books.value = JSON.parse(saved)
      }
    }

    const saveToLocalStorage = () => {
      localStorage.setItem('readingBooks', JSON.stringify(books.value))
    }

    const handleBookSubmit = (book) => {
      books.value.unshift({
        id: Date.now(),
        ...book
      })
      saveToLocalStorage()
      alert('독서 인증이 완료되었습니다!')
      currentView.value = 'list'
    }

    const deleteBook = (id) => {
      if (confirm('정말 삭제하시겠습니까?')) {
        books.value = books.value.filter(book => book.id !== id)
        saveToLocalStorage()
      }
    }

    onMounted(() => {
      loadFromLocalStorage()
    })

    return {
      currentView,
      books,
      handleBookSubmit,
      deleteBook
    }
  }
}
</script>
