<template>
  <div class="space-y-6">
    <h2 class="text-3xl font-bold text-gray-800 mb-6">나의 독서 통계</h2>
    
    <div class="grid grid-cols-1 md:grid-cols-3 gap-6">
      <div class="bg-white rounded-lg shadow-md p-6">
        <div class="flex items-center justify-between">
          <div>
            <p class="text-gray-500 text-sm">총 읽은 책</p>
            <p class="text-3xl font-bold text-indigo-600">{{ totalBooks }}권</p>
          </div>
          <div class="bg-indigo-100 rounded-full p-3">
            <svg class="w-8 h-8 text-indigo-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253"></path>
            </svg>
          </div>
        </div>
      </div>

      <div class="bg-white rounded-lg shadow-md p-6">
        <div class="flex items-center justify-between">
          <div>
            <p class="text-gray-500 text-sm">총 읽은 페이지</p>
            <p class="text-3xl font-bold text-green-600">{{ totalPages }}페이지</p>
          </div>
          <div class="bg-green-100 rounded-full p-3">
            <svg class="w-8 h-8 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"></path>
            </svg>
          </div>
        </div>
      </div>

      <div class="bg-white rounded-lg shadow-md p-6">
        <div class="flex items-center justify-between">
          <div>
            <p class="text-gray-500 text-sm">이번 달 읽은 책</p>
            <p class="text-3xl font-bold text-purple-600">{{ monthlyBooks }}권</p>
          </div>
          <div class="bg-purple-100 rounded-full p-3">
            <svg class="w-8 h-8 text-purple-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z"></path>
            </svg>
          </div>
        </div>
      </div>
    </div>

    <div class="bg-white rounded-lg shadow-md p-6">
      <h3 class="text-xl font-semibold text-gray-800 mb-4">최근 인증한 책</h3>
      <div v-if="books.length === 0" class="text-center py-8 text-gray-500">
        아직 인증한 책이 없습니다. 첫 독서를 인증해보세요!
      </div>
      <div v-else class="space-y-3">
        <div v-for="book in recentBooks" :key="book.id" 
             class="flex items-center justify-between p-4 bg-gray-50 rounded-lg hover:bg-gray-100 transition">
          <div class="flex items-center space-x-4">
            <div class="bg-indigo-100 rounded-lg p-2">
              <svg class="w-6 h-6 text-indigo-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6.253v13m0-13C10.832 5.477 9.246 5 7.5 5S4.168 5.477 3 6.253v13C4.168 18.477 5.754 18 7.5 18s3.332.477 4.5 1.253m0-13C13.168 5.477 14.754 5 16.5 5c1.747 0 3.332.477 4.5 1.253v13C19.832 18.477 18.247 18 16.5 18c-1.746 0-3.332.477-4.5 1.253"></path>
              </svg>
            </div>
            <div>
              <p class="font-semibold text-gray-800">{{ book.title }}</p>
              <p class="text-sm text-gray-500">{{ book.author }} · {{ book.pages }}페이지</p>
            </div>
          </div>
          <span class="text-sm text-gray-400">{{ book.date }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed } from 'vue'

export default {
  name: 'DashBoard',
  props: {
    books: {
      type: Array,
      required: true
    }
  },
  setup(props) {
    const totalBooks = computed(() => props.books.length)
    
    const totalPages = computed(() => {
      return props.books.reduce((sum, book) => sum + book.pages, 0)
    })
    
    const monthlyBooks = computed(() => {
      const currentMonth = new Date().getMonth()
      const currentYear = new Date().getFullYear()
      return props.books.filter(book => {
        const bookDate = new Date(book.date)
        return bookDate.getMonth() === currentMonth && 
               bookDate.getFullYear() === currentYear
      }).length
    })
    
    const recentBooks = computed(() => {
      return props.books.slice(0, 3)
    })

    return {
      totalBooks,
      totalPages,
      monthlyBooks,
      recentBooks
    }
  }
}
</script>
