<template>
  <div class="max-w-2xl mx-auto">
    <div class="bg-white rounded-lg shadow-md p-8">
      <h2 class="text-2xl font-bold text-gray-800 mb-6">독서 인증하기</h2>
      
      <form @submit.prevent="submitBook" class="space-y-6">
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">책 제목</label>
          <input v-model="newBook.title" type="text" required
                 class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                 placeholder="읽은 책의 제목을 입력하세요">
        </div>

        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">저자</label>
          <input v-model="newBook.author" type="text" required
                 class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                 placeholder="저자명을 입력하세요">
        </div>

        <div class="grid grid-cols-2 gap-4">
          <div>
            <label class="block text-sm font-medium text-gray-700 mb-2">페이지 수</label>
            <input v-model.number="newBook.pages" type="number" required min="1"
                   class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                   placeholder="300">
          </div>
          <div>
            <label class="block text-sm font-medium text-gray-700 mb-2">완독 날짜</label>
            <input v-model="newBook.date" type="date" required
                   class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent">
          </div>
        </div>

        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">감상평</label>
          <textarea v-model="newBook.review" rows="4"
                    class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-indigo-500 focus:border-transparent"
                    placeholder="책을 읽고 느낀 점을 작성해주세요"></textarea>
        </div>

        <div>
          <label class="block text-sm font-medium text-gray-700 mb-2">별점</label>
          <div class="flex space-x-2">
            <button v-for="star in 5" :key="star" type="button"
                    @click="newBook.rating = star"
                    class="text-3xl focus:outline-none transition">
              <span :class="star <= newBook.rating ? 'text-yellow-400' : 'text-gray-300'">★</span>
            </button>
          </div>
        </div>

        <button type="submit" 
                class="w-full bg-indigo-600 text-white py-3 rounded-lg font-semibold hover:bg-indigo-700 transition">
          독서 인증하기
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import { ref } from 'vue'

export default {
  name: 'RegisterBook',
  emits: ['submit'],
  setup(props, { emit }) {
    const newBook = ref({
      title: '',
      author: '',
      pages: null,
      date: new Date().toISOString().split('T')[0],
      review: '',
      rating: 5
    })

    const submitBook = () => {
      emit('submit', { ...newBook.value })
      
      // 폼 초기화
      newBook.value = {
        title: '',
        author: '',
        pages: null,
        date: new Date().toISOString().split('T')[0],
        review: '',
        rating: 5
      }
    }

    return {
      newBook,
      submitBook
    }
  }
}
</script>
